// import global stuff
import './src/assets/index.css'
import './src/scss/style.scss'
import Glide from '@glidejs/glide';
import { fixedHeaderToTop, showBackToTopButton, filterOptions } from './src/js/main.js'

const initSlider = () => {
  const hasSlider = document.querySelector('.js-slider');

  if (hasSlider) {
    const slider = new Glide('.js-slider', {
      rewind: false,
      perView: 3,
      gap: 0,
      breakpoints: {
        767: {
          perView: 1,
          focusAt: 'center',
          peek: { before: 5, after: 35 }
        }
      }
    });

    slider.mount();
  }
};

export const onRouteUpdate = () => {
  window.scrollTo(0, 0);
  fixedHeaderToTop();
  showBackToTopButton();
  filterOptions();
  initSlider();
};